<template>
    <div class="search">
        <header>
            <!-- <i class="iconfont icon-jiantou12"></i> -->
            <div class="input">
                <!-- <span @click="toggleType">{{ type }}</span> -->
                <input type="text" v-model="keyWord" placeholder="商品搜索">
            </div>
            <button @click="search()">搜索</button>
            
        </header>
        <div class="search__list">
            <div class="search__list--hot">
                <h5>热门搜索</h5>
                <section>
                    <ul>
                        <router-link
                            v-for="(item,index) in hotKeywords" 
                            :key="index"
                            :to="{
                                path:'/category',
                                query:{
                                    keyword:item
                                }
                            }"
                            tag="li">
                            <a 
                                @click="search(item)">
                                {{ item }}
                            </a>
                        </router-link>
                    </ul>
                </section>
            </div>
            <div class="search__list--history">
                <h5>搜索历史<i class="iconfont icon-clear" @click="clearHistory"></i></h5>
                <section>
                    <ul>
                        <li v-for="(item,index) in history" :key="index" ><a @click="search(item)">{{ item }}</a></li>
                    </ul>
                </section>
            </div>
        </div>
    </div>
</template>

<script>
import qs from 'qs'
export default {
    name: 'search',
    data() {
        return {
            type: '商品',
            history: [],
            hotKeywords: [],
            keyWord:""
        }
    },
    created() {

    },
    methods: {

    },
    activated() {
        this.init()
    },
    methods: {
        /**
         * 初始化数据
         */
        init() {
            this.$http.post(this.url('search&c=index&a=hotkey')).then(({ data: { hotkey } }) => {
                let {
                history,
                    hot_keywords: hotKeywords
            } = hotkey
                history ? this.history = history : this.history = []
                if (hotKeywords) this.hotKeywords = hotKeywords
            })
        },
        /**
         * 点击切换搜索类型
         */
        toggleType() {
            this.type == '商品' ? this.type = '店铺' : this.type = '商品'
        },
        /**
         * 清空搜索历史记录
         */
        clearHistory(){
            this.$http.post(this.url('category&a=clear_history')).then(({data:{status}}) => {
                if(status == 0){
                    this.history = []      
                }
            })
        },
        /**
         * 搜索商品或店铺
         */
        search() {
            this.$router.push({
                name:'category',
                query:{
                    keyword:this.keyWord
                }
            })
        },
    },
    computed: {
        categorySearch() {
            return `index.php?m=category&a=search&keyword=`
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/style/config';
@import '../assets/style/mixins/common';
body{
    background:#fff;
}
header{
    @include disBox();
    padding:1rem .8rem;
    background:#fff;
    margin-bottom:1px;
    .input {
        width: 100%;
        height: 3.2rem;
        line-height: 3.2rem;
        background: #fff;
        color: #777;
        font-size: 1.3rem;
        border-radius: 4px;
        position: relative;
        // padding-left: 5.2rem;
        padding-left: .8rem;
        padding-right: .8rem;
        border:1px solid #eee;
        box-sizing: content-box;
        @include flex1-1();
        span {
            position: absolute;
            font-size:1.4rem;
            left: .8rem;
            height: 3.2rem;
            line-height: 3.2rem;
            &:after{
                position: absolute;
                right: -1rem;
                top:50%;
                margin-top:-.2rem;
                content: '';
                display:block;
                width: 0;
                height: 0;
                border-left: .4rem solid transparent;
                border-right: .4rem solid transparent;
                border-top: .5rem solid #999;
            }
        }
        a {
            @include urlAbsolute();
        }
        input{
            font-size:1.3rem;
            height: 1.8rem;
            line-height: 1.8rem;
            width:100%;
            color:#333;
            border:none;
        }
    }
    .icon-jiantou12{
        font-size:1.8rem;
        margin-right:.6rem;
        height:3.2rem;
        line-height:3.2rem;
        color:#888;
    }
    button{
        margin-left:.6rem;
        background:#eee;
        color:#555;
        height:3.2rem;
        line-height: 3.2rem;
        padding:0 1rem;
        border-radius: 4px;
        font-size:1.3rem;
        color:#333;
        border:1px solid #eee;
        box-sizing: content-box;
    }
}
.search__list{
    background:#fff;
    padding-top:1rem;
    position: absolute;
    width:100%;
    top:5.3rem;
    bottom:0;
    border-top:1px solid #eee;
    overflow-y: auto;
    h5{
        padding:.5rem .9rem;
        font-size:1.3rem;
        font-weight: normal;
        color:#666;
        @include direction(center, space-between)
    }
    section{
        overflow: hidden;
        padding:.9rem;
        padding-top:0;
    }
}

.search__list--hot ul li{
    // width:25%;
    
    float:left;
    padding:.4rem;
    a{
        display:block;
        width:100%;
        text-align: center;
        font-size:1.2rem;
        color:#333;
        padding:.6rem 1.3rem;
        border-radius: 4px;
        border:1px solid #eee;
        white-space: nowrap;
        @include ellipses();
    }
}
.search__list--history ul li{
    border-bottom:1px solid #eee;
    font-size:1.2rem;
    a{
        display: block;
        padding:1.3rem 0;
        color:#333;
    }
    &:last-of-type{
        border-bottom:none;
    }
}
</style>


